import React from 'react';

class Home extends React.Component {

  render() {
    return (
        <div style={bgStyle}>
            <div className={"container h-100"}>
                <div className={"row h-100 justify-content-center align-items-center"}>
                    <form className={"col-lg-8 col-6 col-xs-4"} style={paperStyle}>
                    <div className={"form-group"}>
                        <label for={"formGroupExampleInput"} style={handWriting}>Enter world code:</label>
                        <div className={"input-group"}>
                            <input type={"text"} class={"form-control"} id={"formGroupExampleInput"} placeholder={"Eg. 'CITIA'"}/>
                            <div className={"input-group-append"}>
                                <button className={"btn btn-danger"} type={"button"}>Button</button>
                            </div>
                        </div>
                        
                    </div>
                    </form>   
                </div>
            </div>
        </div>
      
    );
  }
}

//images
const paperImageURL = 'https://i.pinimg.com/originals/47/b5/67/47b567ff37393b5c564cbe1186e90662.jpg';
const bgImageURL = 'https://images.unsplash.com/photo-1510148199876-8a856ee41d42?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&w=1000&q=80';

const handWriting = {
    fontFamily: `'Courgette', 'cursive'`,
    marginBottom: '15px'
}

const paperStyle = {
    backgroundImage: `url(${paperImageURL})`,
    backgroundRepeat: 'noRepeat',
    backgroundSize: 'cover',
    padding: '25px',
    boxShadow: '5px 10px 10px rgba(0,0,0,0.5)',
    color: '#000',
    fontWeight: '500',
    fontSize: '2.5em',
    textAlign: 'center',
    border: '1px solid #1f1715',
    borderTop: '1px solid #473d2b',
}

const bgStyle = {
    backgroundImage: `url(${bgImageURL})`,
    backgroundPosition: 'center',
    backgroundRepeat: 'noRepeat',
    backgroundSize: 'cover',
    width: '100%',
    height: '100%',
    color: 'white'
}

export default Home;
